@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap);
*
{
  box-sizing: border-box;
  margin: 0;
  padding:0;
  /* background: rgb(100, 100, 100); */
  /* colo */
}

a
{
  text-decoration: none;
  color: teal;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #282c34;
  color: white;
  max-width: 1200px;
  margin: 0 auto;
}

.App{text-align:left;padding:20px 0}.match-won{background-color:#559c55}.match-lost{background-color:#ac4646}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;border-radius:5px;box-shadow:rgba(102,102,102,0.4) 0px 2px 4px,rgba(102,102,102,0.3) 0px 7px 13px -3px,rgba(102,102,102,0.2) 0px -3px 0px inset;padding:20px;margin:10px 0}.MatchDetailCard span{font-size:1.2rem;font-weight:bold;color:white;display:block}.MatchDetailCard .innings-details{padding:15px 10px}.MatchDetailCard .innings-details .details-date,.MatchDetailCard .innings-details .details-venue,.MatchDetailCard .innings-details .details-winner{padding-top:10px}.MatchDetailCard .innings-extra-details{padding:15px 10px;display:flex;flex-direction:column;justify-content:space-around;align-items:flex-end;font-size:1.2rem;text-align:right}

.MatchPage h1{margin-bottom:20px}.MatchPage select{width:200px;height:40px;font-size:15px;font-weight:bold;padding-left:5px;border-radius:5px;margin-bottom:20px}.MatchPage .title-bar{display:flex;justify-content:space-between;align-items:center}.MatchPage .title-bar .back{width:100px;height:40px;font-size:15px;font-weight:bold;padding:10px 20px;border:solid 1px #ccc;box-shadow:rgba(0,0,0,0.4) 0px 2px 4px,rgba(0,0,0,0.3) 0px 7px 13px -3px,rgba(0,0,0,0.2) 0px -3px 0px inset}.MatchPage .title-bar .back:hover{background:#ccc;transition:0.5s}

.MatchSmallCard{display:flex;flex-direction:column;justify-content:center;padding:20px;text-align:left;width:100%;height:100%;border-radius:5px;box-shadow:rgba(102,102,102,0.4) 0px 2px 4px,rgba(102,102,102,0.3) 0px 7px 13px -3px,rgba(102,102,102,0.2) 0px -3px 0px inset;overflow:hidden;cursor:pointer;font-size:large}.MatchSmallCard span{font-size:1.2rem;font-weight:bold;color:white;display:block}.MatchSmallCard+.MatchSmallCard{margin-left:10px}

.teamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:5px}.teamPage .team-name{padding:0 10px;font-size:2.5rem;font-weight:bold;text-align:center;grid-column:span 3;justify-self:start;align-self:center}.teamPage .win-loss{font-size:1.5rem;text-align:center;grid-column:span 1;justify-self:center;align-self:center;padding:50px}.teamPage .win-loss .ratio-text{font-size:1.1rem}.teamPage .match-detail{font-size:1.5rem;grid-column:span 4}.teamPage .match-detail h4{padding:0 20px}.teamPage .match-small{font-size:1.5rem;text-align:center;margin-bottom:10px;grid-column:span 3;display:flex;justify-content:center;align-items:center}.teamPage .match-more{font-size:1.5rem;text-align:center;margin-bottom:10px;grid-column:span 1;display:flex;justify-content:center;align-items:center}.teamPage .match-more span{border:1px solid white;width:100px;height:100px;border-radius:50%;display:flex;justify-content:center;align-items:center;box-shadow:rgba(0,0,0,0.4) 0px 2px 4px,rgba(0,0,0,0.3) 0px 7px 13px -3px,rgba(0,0,0,0.2) 0px -3px 0px inset}.teamPage .match-more span:hover{background-color:white;color:black;cursor:pointer;transition:all 0.3s ease-in-out}.back{position:absolute;top:50px;width:100px;height:40px;font-size:15px;font-weight:bold;padding:10px 20px;border:solid 1px #ccc;box-shadow:rgba(0,0,0,0.4) 0px 2px 4px,rgba(0,0,0,0.3) 0px 7px 13px -3px,rgba(0,0,0,0.2) 0px -3px 0px inset}.back:hover{background:#ccc;transition:0.5s}

.HomePage{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:20px;grid-row-gap:20px;margin-bottom:20px}.HomePage h2{text-align:center}.HomePage .team{border:solid 1px #ccc;font-size:2rem;width:100%;height:250px;display:flex;justify-content:center;align-items:center;border-radius:5px;box-shadow:rgba(102,102,102,0.4) 0px 2px 4px,rgba(102,102,102,0.3) 0px 7px 13px -3px,rgba(102,102,102,0.2) 0px -3px 0px inset}.HomePage .team:hover{transition:all .3s ease;-webkit-transform:scale(1.05);transform:scale(1.05);background-color:teal;cursor:pointer;color:white}.team-text{font-size:3rem;margin-bottom:20px}

